import VueGalleria from "./VueGalleria";
export default {
  name: 'VueSwiper',
  components: {VueGalleria},
  data () {
    return {
      zoomIndex: null,
      zoom: false,
      images: [
        {
          'name': 'image1',
          'text': 'Подпись к фото. В Санкт-Петербурге основана. Императорская Академия художеств.',
          'src': require('../assets/image/image32.jpg'),
        },
        {
          'name': 'image1',
          'text': 'Подпись к фото. В Санкт-Петербурге основана. Императорская Академия художеств.',
          'src': require('../assets/image/photos/pic5.png'),
        },
        {
          'name': 'image1',
          'text': 'Подпись к фото. В Санкт-Петербурге основана. Императорская Академия художеств.',
          'src': require('../assets/image/photos/pic4.png'),
        },
        {
          'name': 'image1',
          'text': 'Подпись к фото. В Санкт-Петербурге основана. Императорская Академия художеств.',
          'src': require('../assets/image/photos/pic3.png'),
        },
        {
          'name': 'image1',
          'text': 'Подпись к фото. В Санкт-Петербурге основана. Императорская Академия художеств.',
          'src': require('../assets/image/photos/pic1.png'),
        },
      ],
      selected: 'Technologies',
      loader: false,
      openSelect: false,
      typePage: 'bullets',
      swiperOptionSlider: {
        centeredSlides: true,
        spaceBetween: 10,
        slidesPerView :100,
      },
      swiperOption: {
        spaceBetween: 10,
        slidesPerView :'auto',
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.arrows__btn_next',
          prevEl: '.arrows__btn_prev'
        }
      }
    }
  },
  props: ['text', 'simvol'],
  created() {
    this.$nuxt.$on('closeGalleria', () => {
      document.getElementsByTagName('body')[0].style.overflow = 'auto'
      this.zoomIndex = null
      this.zoom = false
    })
  },
  mounted() {
    if (window.innerWidth > 850) {
      this.swiperOption.pagination.type = 'bullets'
      this.swiperOption.pagination.renderBullet = function (index, className) {
        return '<span class="' + className + '">' + (index + 1) + "</span>";
      }
    } else {
      if (this.simvol === '/') {
        this.swiperOption.pagination.type = 'custom'
        this.swiperOption.pagination.renderCustom = function (swiper, current, total) {
          return current + ' / ' + (total - 1);
        }
      } else {
        this.swiperOption.pagination.type = 'custom'
        this.swiperOption.pagination.renderCustom = function (swiper, current, total) {
          return current + '<div class="num-lin"></div>' + (total - 1);
        }
      }

    }
    this.loader = true
  },
  methods: {
    zoomImage (index) {
      document.getElementsByTagName('body')[0].style.overflow = 'hidden'
      this.zoomIndex = index
      this.zoom = true
    }
  }
}
