export default {
  name: "VueGalleria",
  props: ['images', 'index'],
  methods: {
    browseLeft () {
      if (this.index > 0) {
        this.index --
      } else {
        this.index = this.images.length - 1
      }
    },
    browseRight () {
      if (this.index + 1 < this.images.length) {
        this.index ++
      } else {
        this.index = 0
      }
    }
  }
}
